<template><div class="markdown-body"><h1>选项卡</h1>
<p><code v-pre="">mu-tabs</code> 使在不同的视图和功能间探索和切换以及浏览不同类别的数据集合起来变得简单。</p>
<h2 id="shi-li"><a class="header-anchor" href="#shi-li" aria-hidden="true">¶</a> 示例</h2>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;mu-container&gt;\n  &lt;mu-tabs :value.sync=\&quot;active\&quot;&gt;\n    &lt;mu-tab&gt;TAB ITEM 1&lt;/mu-tab&gt;\n    &lt;mu-tab&gt;TAB ITEM 2&lt;/mu-tab&gt;\n    &lt;mu-tab&gt;TAB ITEM 3&lt;/mu-tab&gt;\n  &lt;/mu-tabs&gt;\n  &lt;div class=\&quot;demo-text\&quot; v-if=\&quot;active === 0\&quot;&gt;\n    &lt;p&gt;“……是的，我承认从很早以前我都害怕死亡，在医院里被人研究的时候，以前做出布局的时候，其实我很怕死的，因为除了死以外，我没有值得珍惜的东西，很奇怪是吧？恰好是这样我真的很怕死，因为我想要有值得珍惜的东西之后，死亡之后才会不寂寞，我才能够安然的面对死亡……”&lt;/p&gt;\n  &lt;/div&gt;\n  &lt;div class=\&quot;demo-text\&quot; v-if=\&quot;active === 1\&quot;&gt;\n    &lt;p&gt;“我的心从来没有这么坚定过，所以我会为了补偿而死，也可以为了补偿而死……一辈子，急辈子都无所谓，我绝不后退！”&lt;/p&gt;\n    &lt;p&gt;“如果我后退呢？如果我想要死呢？我不想你再次背对着我逃跑了……”&lt;/p&gt;\n    &lt;p&gt;“那么就去地狱找到你，我绝对不逃！”&lt;/p&gt;\n    &lt;p&gt;“白痴，你也哭了？因为那些软弱拖累你的脚步？”&lt;/p&gt;\n  &lt;/div&gt;\n  &lt;div class=\&quot;demo-text\&quot; v-if=\&quot;active === 2\&quot;&gt;\n    &lt;p&gt;“不，这泪水……是因为勇气，因为力量，因为信任，……你不会懂的！”&lt;/p&gt;\n    &lt;p&gt;“我不会帮你，想要什么样的未来……自己去追寻吧！”&lt;/p&gt;\n    &lt;p&gt;“我不需要你的帮忙！未来我会一手开启，什么样的敌人我也不会惧怕……还有，其实我们可以成为朋友的……”&lt;/p&gt;\n    &lt;p&gt;“也许吧，未来……给你了。”&lt;/p&gt;\n  &lt;/div&gt;\n&lt;/mu-container&gt;\n\n\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n  data () {\n    return {\n      active: 0\n    };\n  }\n}\n&quot;,&quot;style&quot;:&quot;\n.demo-text {\n  padding: 16px;\n  background: #fff;\n  p {\n    margin: 8px 0;\n  }\n}\n&quot;}">
                    <div class="source" slot="source"><mu-container>
  <mu-tabs :value.sync="active">
    <mu-tab>TAB ITEM 1</mu-tab>
    <mu-tab>TAB ITEM 2</mu-tab>
    <mu-tab>TAB ITEM 3</mu-tab>
  </mu-tabs>
  <div class="demo-text" v-if="active === 0">
    <p>“……是的，我承认从很早以前我都害怕死亡，在医院里被人研究的时候，以前做出布局的时候，其实我很怕死的，因为除了死以外，我没有值得珍惜的东西，很奇怪是吧？恰好是这样我真的很怕死，因为我想要有值得珍惜的东西之后，死亡之后才会不寂寞，我才能够安然的面对死亡……”</p>
  </div>
  <div class="demo-text" v-if="active === 1">
    <p>“我的心从来没有这么坚定过，所以我会为了补偿而死，也可以为了补偿而死……一辈子，急辈子都无所谓，我绝不后退！”</p>
    <p>“如果我后退呢？如果我想要死呢？我不想你再次背对着我逃跑了……”</p>
    <p>“那么就去地狱找到你，我绝对不逃！”</p>
    <p>“白痴，你也哭了？因为那些软弱拖累你的脚步？”</p>
  </div>
  <div class="demo-text" v-if="active === 2">
    <p>“不，这泪水……是因为勇气，因为力量，因为信任，……你不会懂的！”</p>
    <p>“我不会帮你，想要什么样的未来……自己去追寻吧！”</p>
    <p>“我不需要你的帮忙！未来我会一手开启，什么样的敌人我也不会惧怕……还有，其实我们可以成为朋友的……”</p>
    <p>“也许吧，未来……给你了。”</p>
  </div>
</mu-container>



</div>
                    
                    <div class="highlight" slot="highlight"><pre v-pre="" class="hljs"><code v-pre=""><span class="hljs-tag">&lt;<span class="hljs-name">mu-container</span>&gt;</span>
