<td>color</td>
<td>颜色</td>
<td>String</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>indicator-color</td>
<td>底部高亮线颜色</td>
<td>String</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>inverse</td>
<td>是否启动反色模式，将更改组件的样式</td>
<td>Boolean</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>full-width</td>
<td>mu-tab 是否自动适应父元素宽度</td>
<td>Boolean</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>center</td>
<td>mu-tab 是否居中显示</td>
<td>Boolean</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>value</td>
<td>用于控制 mu-tab 激活状态</td>
<td>—</td>
<td>—</td>
<td>0</td>
</tr>
</tbody>
</table>
<h2 id="tabs-events"><a class="header-anchor" href="#tabs-events" aria-hidden="true">¶</a> Tabs Events</h2>
<table>
<thead>
<tr>
<th>参数</th>
<th>介绍</th>
<th>回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>change</td>
<td>当 <code v-pre="">mu-tab</code> 被点击时触发事件</td>
<td>(value)</td>
</tr>
</tbody>
</table>
<h2 id="tab-props"><a class="header-anchor" href="#tab-props" aria-hidden="true">¶</a> Tab Props</h2>
<table>
<thead>
<tr>
<th>参数</th>
<th>介绍</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>disabled</td>
<td>是否禁用</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>value</td>
<td>用于控制是否激活，默认是该组件的下标值</td>
<td>—</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>ripple</td>
<td>是否有波纹效果</td>
<td>Boolean</td>
<td>—</td>
<td>true</td>
</tr>
<tr>
<td>href</td>
<td>相当于 a 标签的 href属性，设置之后会渲染成 a 标签，默认是使用button标签</td>
<td>String</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>to</td>
<td>表示目标路由的链接。设置之后组件会被渲染成<code v-pre="">router-link</code>, 当被点击后，内部会立刻把 <code v-pre="">to</code> 的值传到 <code v-pre="">router.push()</code>，所以这个值可以是一个字符串或者是描述目标位置的对象。</td>
<td>String/Object</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>tag</td>
<td>有时候想要 <code v-pre="">&lt;router-link&gt;</code> 渲染成某种标签，例如 <code v-pre="">&lt;li&gt;</code>。 于是我们使用 <code v-pre="">tag prop</code> 类指定何种标签，同样它还是会监听点击，触发导航。</td>
<td>String</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>active-class</td>
<td>设置 链接激活时使用的 CSS 类名。默认值可以通过路由的构造选项 <code v-pre="">linkActiveClass</code> 来全局配置。</td>
<td>String</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>event</td>
<td>声明可以用来触发导航的事件。可以是一个字符串或是一个包含字符串的数组</td>
<td>String/Array</td>
<td>—</td>
<td>click</td>
</tr>
<tr>
<td>exact</td>
<td>“是否激活” 默认类名的依据是 <strong>inclusive match</strong> （全包含匹配）。 举个例子，如果当前的路径是 <code v-pre="">/a</code> 开头的，那么 <code v-pre="">&lt;router-link to=&quot;/a&quot;&gt;</code> 也会被设置 CSS 类名。</td>
<td>Boolean</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>exact-active-class</td>
<td>配置当链接被精确匹配的时候应该激活的 class。注意默认值也是可以通过路由构造函数选项 linkExactActiveClass 进行全局配置的。</td>
<td>String</td>
<td>—</td>
<td>router-link-exact-active</td>
</tr>
<tr>
<td>append</td>
<td>设置 <code v-pre="">append</code> 属性后，则在当前（相对）路径前添加基路径。例如，我们从 <code v-pre="">/a</code> 导航到一个相对路径 b，如果没有配置 append，则路径为 /b，如果配了，则为 /a/b</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>replace</td>
<td>设置 <code v-pre="">replace</code> 属性的话，当点击时，会调用 <code v-pre="">router.replace()</code> 而不是 <code v-pre="">router.push()</code>，于是导航后不会留下 history 记录。</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
</tbody>
</table>


</div class="markdown-body"></template>
<style lang="less">
.demo-text {
  padding: 16px;
  background: #fff;
  p {
    margin: 8px 0;
  }
}
</style>
<script>
export default {
  data () {
    return {
      active: 0,
      active1: 0,
      active2: 0,
      active3: 0
    };
  }
}
</script>


